body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #121212;
  font-family: 'Arial', sans-serif;
}

.calculator {
  background-color: #1e1e1e;
  border-radius: 15px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.5);
  padding: 20px;
  width: 360px;
}

input[type="text"] {
  width: 92%;
  height: 70px;
  border: none;
  border-radius: 10px;
  padding: 15px;
  font-size: 2.5em;
  text-align: right;
  color: #ffffff;
  background-color: #2c2c2c;
  margin-bottom: 15px;
}

input[type="text"]::placeholder {
  color: #aaaaaa;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
}

button {
  height: 70px;
  font-size: 1.8em;
  background-color: #444;
  border: none;
  border-radius: 10px;
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s;
}

button:hover {
  background-color: #555;
}

button:active {
  transform: scale(0.95);
}

button:nth-child(3),
button:nth-child(4),
button:nth-child(8),
button:nth-child(12) {
  background-color: #f39c12;
}

button:nth-child(3):hover,
button:nth-child(4):hover,
button:nth-child(8):hover,
button:nth-child(12):hover {
  background-color: #e67e22;
}

button.zero {
  grid-column: span 2;
}
